body {
  background: #e9967a;
}
h1 {
  color: white;
  text-align: center;
  margin-top: 40px;
  font-style: italic;
  font-weight: bold;
  font-size: 50px;
  font-family: 'Times New Roman', Times, serif;
}

p {
  color: white;
  text-align: center;
  font-weight: bold;
  font-size: 19px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.slider {
  position: relative;
  height: 76vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image {
  height: 600px;
  border-radius: 10px;
  object-fit: cover;
  width: 100%;
  max-width: 980px;
}

.right-arrow {
  position: absolute;
  top: 50%;
  max-width: 980px;
  right: 22%;
  font-size: 3rem;
  color: white;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 22%;
  font-size: 3rem;
  color: white;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}
